/* Container principale */
.piatti-container {
  padding: 24px;
  max-width: 1400px;
  margin: 0 auto;
  background: linear-gradient(135deg, #fafafa 0%, #f5f5f5 100%);
  min-height: calc(100vh - 64px);
}

/* Header Section */
.header-section {
  margin-bottom: 32px;
  text-align: center;
}

.header-section h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin: 0 0 24px 0;
  color: #d32f2f;
  font-size: 2.2rem;
  font-weight: 600;
}

.header-section h2 mat-icon {
  font-size: 2.4rem;
  color: #f44336;
}

.search-section {
  max-width: 500px;
  margin: 0 auto 16px;
}

.search-field {
  width: 100%;
}

.stats-info {
  color: #666;
  font-size: 1rem;
  margin-top: 8px;
}

.piatti-count {
  background: rgba(211, 47, 47, 0.1);
  color: #d32f2f;
  padding: 4px 12px;
  border-radius: 16px;
  font-weight: 500;
}

/* Loading Section */
.loading-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
  color: #666;
}

.loading-section p {
  margin-top: 16px;
  font-size: 1.1rem;
}

/* Griglia Piatti - Layout moderno con card belle */
.piatti-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 32px;
  margin-top: 24px;
}

/* Card Piatto - Design moderno e accattivante */
.piatto-card {
  border-radius: 16px;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  border: 1px solid rgba(0, 0, 0, 0.08);
  background: white;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.08);
}

.piatto-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 32px rgba(211, 47, 47, 0.15);
  border-color: rgba(211, 47, 47, 0.2);
}

/* Immagine principale con overlay elegante */
.card-image-container {
  position: relative;
  width: 100%;
  height: 240px;
  overflow: hidden;
  background: linear-gradient(135deg, #ffebee 0%, #fce4ec 100%);
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.piatto-card:hover .card-image {
  transform: scale(1.1);
}

.card-image-container::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50%;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.3));
  pointer-events: none;
}

/* Badge Prezzo - Design elegante */
.price-badge {
  position: absolute;
  top: 16px;
  left: 16px;
  background: linear-gradient(135deg, #d32f2f, #f44336);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 1.1rem;
  box-shadow: 0 4px 12px rgba(211, 47, 47, 0.4);
  z-index: 2;
}

/* Badge Disponibilità */
.availability-badge {
  position: absolute;
  top: 16px;
  right: 16px;
  padding: 8px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.availability-badge.disponibile {
  background: rgba(76, 175, 80, 0.95);
  color: white;
}

.availability-badge.non-disponibile {
  background: rgba(244, 67, 54, 0.95);
  color: white;
}

.availability-badge mat-icon {
  font-size: 20px;
  width: 20px;
  height: 20px;
}

/* Header della card */
.piatto-card mat-card-header {
  padding: 20px 20px 8px 20px;
}

.piatto-card mat-card-title {
  font-size: 1.4rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 4px;
  line-height: 1.3;
}

.piatto-card mat-card-subtitle {
  color: #666;
  font-size: 0.9rem;
  line-height: 1.4;
}

/* Contenuto della card */
.piatto-card mat-card-content {
  padding: 0 20px 16px 20px;
}

.description {
  color: #555;
  font-size: 0.95rem;
  line-height: 1.5;
  margin-bottom: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Sezione Ingredienti */
.ingredienti-section {
  margin: 20px 0;
}

.ingredienti-section h4 {
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 0 0 12px 0;
  color: #d32f2f;
  font-size: 1rem;
  font-weight: 600;
}

.ingredienti-section h4 mat-icon {
  font-size: 18px;
  color: #f44336;
}

.ingredienti-chips {
  max-height: 80px;
  overflow-y: auto;
}

.ingredienti-chips mat-chip-listbox {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.ingredienti-chips mat-chip-option {
  background: #ffebee !important;
  color: #d32f2f !important;
  border: 1px solid rgba(211, 47, 47, 0.2) !important;
  font-size: 0.8rem;
  padding: 4px 8px !important;
  height: auto !important;
  min-height: 28px !important;
}

/* Statistiche piatto */
.piatto-stats {
  display: flex;
  gap: 16px;
  margin: 16px 0;
  padding: 12px;
  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #d32f2f;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #666;
  font-size: 0.9rem;
}

.stat-item mat-icon {
  font-size: 16px;
  color: #d32f2f;
}

/* Sezione warning */
.warning-section {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  padding: 12px;
  background: #fff3e0;
  border-radius: 8px;
  border-left: 4px solid #ff9800;
  margin: 16px 0;
}

.warning-section mat-icon {
  color: #ff9800;
  font-size: 20px;
  margin-top: 2px;
}

.warning-section span {
  color: #e65100;
  font-size: 0.9rem;
  line-height: 1.4;
}

/* Azioni della card */
.piatto-card mat-card-actions {
  padding: 16px 20px 20px 20px;
  background: #fafafa;
  border-top: 1px solid #f0f0f0;
}

.actions-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.primary-actions {
  display: flex;
  gap: 8px;
  flex: 1;
}

.primary-actions button {
  flex: 1;
  font-weight: 500;
  border-radius: 8px;
  padding: 12px 16px;
  transition: all 0.3s ease;
}

.primary-actions button mat-icon {
  margin-right: 6px;
  font-size: 18px;
}

.primary-actions button[color="primary"] {
  background: linear-gradient(135deg, #d32f2f, #f44336) !important;
}

.primary-actions button[color="primary"]:hover {
  background: linear-gradient(135deg, #b71c1c, #d32f2f) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(211, 47, 47, 0.4);
}

.primary-actions button[color="accent"] {
  background: #fff !important;
  color: #d32f2f !important;
  border: 2px solid #d32f2f !important;
}

.primary-actions button[color="accent"]:hover {
  background: #ffebee !important;
  transform: translateY(-2px);
}

.secondary-actions {
  display: flex;
  gap: 4px;
}

.secondary-actions button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.secondary-actions button:hover {
  background: #f0f0f0;
  transform: scale(1.1);
}

.secondary-actions button[color="primary"] mat-icon {
  color: #d32f2f;
}

/* Messaggio nessun piatto */
.no-piatti {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
  text-align: center;
}

.no-piatti mat-icon {
  font-size: 4rem;
  width: 4rem;
  height: 4rem;
  color: #bbb;
  margin-bottom: 16px;
}

.no-piatti h3 {
  margin: 0 0 12px 0;
  color: #666;
  font-size: 1.5rem;
}

.no-piatti p {
  color: #999;
  font-size: 1rem;
  margin-bottom: 24px;
  max-width: 400px;
  line-height: 1.5;
}

.no-piatti-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  justify-content: center;
}

.no-piatti-actions button {
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 500;
}

.no-piatti-actions button mat-icon {
  margin-right: 8px;
}

/* Call-to-Action Section */
.cta-section {
  margin-top: 40px;
}

.cta-card {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
  border-radius: 16px;
  border: 2px solid #ffebee;
  background: linear-gradient(135deg, #fff 0%, #ffebee 100%);
}

.cta-content {
  padding: 24px;
}

.cta-content mat-icon {
  font-size: 3rem;
  width: 3rem;
  height: 3rem;
  color: #d32f2f;
  margin-bottom: 16px;
}

.cta-content h3 {
  margin: 0 0 12px 0;
  color: #d32f2f;
  font-size: 1.4rem;
  font-weight: 600;
}

.cta-content p {
  color: #666;
  margin-bottom: 24px;
  line-height: 1.5;
}

.cta-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-actions button {
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 500;
}

.cta-actions button mat-icon {
  margin-right: 8px;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .piatti-grid {
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 24px;
  }
}

@media (max-width: 768px) {
  .piatti-container {
    padding: 16px;
  }
  
  .header-section h2 {
    font-size: 1.8rem;
  }
  
  .piatti-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .piatto-card {
    max-width: 100%;
  }
  
  .card-image-container {
    height: 200px;
  }
  
  .actions-row {
    flex-direction: column;
    gap: 12px;
  }
  
  .primary-actions {
    width: 100%;
  }
  
  .secondary-actions {
    align-self: flex-end;
  }
  
  .cta-actions {
    flex-direction: column;
  }
  
  .no-piatti-actions {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .piatti-container {
    padding: 12px;
  }
  
  .header-section h2 {
    font-size: 1.6rem;
  }
  
  .card-image-container {
    height: 180px;
  }
  
  .piatto-card mat-card-header,
  .piatto-card mat-card-content,
  .piatto-card mat-card-actions {
    padding-left: 16px;
    padding-right: 16px;
  }
  
  .primary-actions {
    flex-direction: column;
  }
  
  .primary-actions button {
    width: 100%;
  }
}

/* Animazioni personalizzate */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.piatto-card {
  animation: fadeInUp 0.6s ease-out;
}

.piatto-card:nth-child(even) {
  animation-delay: 0.1s;
}

.piatto-card:nth-child(3n) {
  animation-delay: 0.2s;
}

/* Stili scrollbar personalizzati */
.ingredienti-chips::-webkit-scrollbar {
  width: 4px;
}

.ingredienti-chips::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 2px;
}

.ingredienti-chips::-webkit-scrollbar-thumb {
  background: #d32f2f;
  border-radius: 2px;
}

.ingredienti-chips::-webkit-scrollbar-thumb:hover {
  background: #b71c1c;
}
